import { defineComponent } from 'vue';
import './index.scss';
const initDefaultProps = {
    value: 0,
    max: 5
};
const AweRate = defineComponent({
    name: 'AweRate',
    setup(_, { attrs }) {
        const props = attrs;
        const { value, max } = Object.assign(Object.assign({}, initDefaultProps), props);
        const renderRates = () => {
            const arr = [];
            for (let i = 0; i < max; i++) {
                arr.push(i);
            }
            return arr.map((o) => {
                if ((o + 1) > value) {
                    return (<i class="iconfont icon-collection"/>);
                }
                else {
                    return (<i class="iconfont icon-collection-fill"/>);
                }
            });
        };
        return () => (<div class="rate">
        {renderRates()}
      </div>);
    }
});
AweRate.install = function (app) {
    app.component(AweRate.name, AweRate);
};
export default AweRate;
