// export default nSteps;
import { defineComponent } from 'vue';
import './index.scss';
import classNames from '../../src/utils/className';
const AweStep = defineComponent({
    name: 'AweStep',
    setup(_, { slots, attrs }) {
        const { vertical, active } = attrs;
        console.log(vertical);
        const renderChild = (child, index) => {
            const setpProps = Object.assign(Object.assign({}, child.props), { index,
                vertical, active: index === active - 1, isLatest: index === slots.default().length - 1 });
            return (<AweStep {...setpProps}/>);
        };
        const stepsClass = () => {
            const classList = ['awe-steps', vertical ? 'vertical' : 'horizontal'];
            return classNames(classList);
        };
        return () => (<div class={stepsClass()}>
        {(slots && slots.default()).map((child, index) => {
                return renderChild(child, index);
            })}
      </div>);
    }
});
AweStep.install = function (app) {
    app.component(AweStep.name, AweStep);
    return app;
};
export default AweStep;
