import { defineComponent } from 'vue';
import classNames from '../../src/utils/className';
import './index.scss';
const TableProps = {
    columns: {
        type: Array,
        default: function () {
            return [];
        }
    },
    data: {
        type: Array,
        default: function () {
            return [];
        }
    },
    border: {
        type: [Boolean, String],
        default: true
    },
    rowClick: {
        type: Function,
        default: null
    },
    rowDbclick: {
        type: Function,
        default: null
    }
};
const AweTable = defineComponent({
    name: 'AweTable',
    props: TableProps,
    setup(props) {
        const currentColumns = props.columns.map(item => {
            return Object.assign({ width: 200, align: 'left', title: '' }, item);
        });
        const tdClass = classNames(['tabel-td', props.border ? 'border' : '']);
        const dealTableData = (currentDataItem, currentColumns) => {
            const res = [];
            currentColumns.forEach((item) => {
                res.push(currentDataItem[item.dataIndex]);
            });
            return res;
        };
        const onClickRow = (dataItem, e) => {
            props.rowClick(dataItem, e);
        };
        const onDbClickRow = (dataItem, e) => {
            props.rowDbclick(dataItem, e);
        };
        const rednerThead = () => {
            const res = currentColumns.map((colum, index) => {
                return <th key={index} style={{ width: colum.width + 'px', textAlign: colum.align }}>{colum.title}</th>;
            });
            return (<thead><tr>{res}</tr></thead>);
        };
        const renderThbody = () => {
            const res = props.data.map((dataItem, index) => {
                return <tr key={index} onClick={e => onClickRow(dataItem, e)} onDblclick={e => onDbClickRow(dataItem, e)}>
          {renderTd(dataItem, currentColumns)}
        </tr>;
            });
            return (<tbody>{res}</tbody>);
        };
        const renderTd = (dataItem, currentColumns) => {
            const tableData = dealTableData(dataItem, currentColumns);
            const res = tableData.map((tditem, itemIndex) => {
                return <td key={itemIndex} class={tdClass} style={{ textAlign: currentColumns[itemIndex].align }}>{tditem}</td>;
            });
            return res;
        };
        return () => (<div class="tabel-content">
        <table class="base-table">
            {rednerThead()}
            {renderThbody()}
        </table>
      </div>);
    }
});
AweTable.install = function (app) {
    app.component(AweTable.name, AweTable);
};
export default AweTable;
