import { defineComponent } from 'vue';
const initDefaultProps = {
    gutter: '',
    justify: 'center',
    align: 'center'
};
const AweRow = defineComponent({
    name: 'AweRow',
    setup(_, { slots, attrs }) {
        const props = attrs;
        const { gutter, align, justify } = Object.assign(Object.assign({}, initDefaultProps), props);
        const gutterStyle = () => {
            const temp = gutter ? { marginLeft: -gutter + 'px', marginRight: -gutter + 'px' } : '';
            const alignItems = align === 'top'
                ? 'flex-start'
                : align === 'middle'
                    ? 'center'
                    : align === 'bottom' ? 'flex-end' : '';
            return Object.assign({ display: 'flex', 'justify-content': justify, 'align-items': alignItems }, temp);
        };
        return () => (<div style={Object.assign({}, gutterStyle())}>
        {slots.default && slots.default()}
      </div>);
    }
});
AweRow.install = function (Vue) {
    Vue.component(AweRow.name, AweRow);
};
export default AweRow;
