import { defineComponent } from 'vue';
const AweIcon = defineComponent({
    name: 'AweIcon',
    setup(_, { attrs }) {
        const props = attrs;
        const { name } = props;
        return () => (<i class={'iconfont ' + name}/>);
    }
});
AweIcon.install = function (app) {
    app.component(AweIcon.name, AweIcon);
    return app;
};
export default AweIcon;
