import { defineComponent, Transition, reactive, createApp } from 'vue';
import './index.scss';
import classNames from '../../src/utils/className';
import AweIcon from '../awe-icon';
const initDefaultProps = {
    duration: 0,
    message: '',
    type: '',
    messageTitle: '',
    hasTitle: false
};
const Message = defineComponent({
    name: 'Message',
    setup(_, { attrs }) {
        const props = attrs;
        const data = reactive({
            show: true,
            timer: null
        });
        const { type, messageTitle, hasTitle, message, duration } = Object.assign(Object.assign({}, initDefaultProps), props);
        const messageClassList = classNames(['base-message', type]);
        const firstIconClassList = classNames([hasTitle ? 'icon-title' : '']);
        const secondIconClassList = classNames([`icon-${type}`]);
        const closeMessage = () => {
            data.show = false;
            data.timer = null;
        };
        const startTimer = () => {
            if (duration > 0) {
                data.timer = setTimeout(() => {
                    closeMessage();
                }, duration);
            }
        };
        startTimer();
        return () => (data.show
            ? <Transition name="awemessage">
          <div class={messageClassList}>
            <div class="message-content">
              <div class="content-cell">
                <AweIcon name={`icon-${type}`} class={firstIconClassList}/>
              </div>
              <div class="content-cell u-full">
                {hasTitle && <span class="message-title">{messageTitle}</span>}
                <p>{message}</p>
                {type !== 'info' &&
                    <AweIcon id="messageCloseIcon" name="icon-times" class={secondIconClassList} onClick={closeMessage}/>}
              </div>
            </div>
          </div>
      </Transition>
            : null);
    }
});
const AweMessage = function (options) {
    const defaults = {
        type: 'info',
        message: '一般的文字信息，默认左对齐，可以带图标。',
        duration: 3000
    };
    for (const key in options) {
        defaults[key] = options[key];
    }
    const div = document.createElement('div');
    document.body.appendChild(div);
    const app = createApp({
        render() {
            return <Message {...defaults}/>;
        }
    });
    app.mount(div);
};
export default AweMessage;
